/* button */

.navigate-button {
  background-color: #4f37d7; /* A nice green color */
  color: white; /* Text color */
  padding: 12px 24px; /*Padding for the button */
  font-size: 16px; /* Font size */
  border: none; /* Remove default border */
  border-radius: 5px; /* Rounded corners */
  cursor: pointer; /* Show pointer cursor on hover */
  transition: background-color 0.3s ease, box-shadow 0.3s ease; /* Smooth transition for hover effects */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  margin-top: 200px;
  margin-right: 0px;
}

.navigate-button:hover {
  background-color: #45a049; /* Slightly darker green on hover */
  box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.2); /* Add a stronger shadow on hover */
}

.navigate-button:active {
  background-color: #388e3c; /* Even darker green when clicked */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); /* Reduce shadow on active */
}

